      if (window.userName != this.xmkbInfo.yfzrrxm) {
        this.Operatepower = false;
      } else {
        this.Operatepower = true;
      }
    }
    // 
    this.getItemUserNum();
  },
  methods: {
    handleChangeRwlx(val) {
      this.getProjectCatalog();
    },
    handleChangeRwzt(val) {
      this.getProjectCatalog();
    },
    checkSkipDetail(data) {
      // 
      this.tabsLabel = data;
      if (this.tabsLabel == "process") {
        this.getProjectCatalog();
      }
    },
    // 
    handleCommitMilestone() {
      this.getMilestone(this.fbcpmc, this.fbcpbh);
      this.commitlcbVisible = false;
    },
    //   
    handleTabsClick(data) {
      this.tabsLabel = data;
      if (data == "process") {
        // 
        this.getProjectCatalog();
      }
    },

    //  
    commitTaskProcess() {
      this.date =
        new Date().getFullYear() +
        "-" +
        ("0" + (new Date().getMonth() + 1)).slice(-2) +
        "-" +
        ("0" + new Date().getDate()).slice(-2);
      if (!this.ProcessValue) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return;
      }
      this.commitProcess(1);
    },
    // 
    remindeTaskProcess() {
      this.date =
        new Date().getFullYear() +
        "-" +
        ("0" + (new Date().getMonth() + 1)).slice(-2) +
        "-" +
        ("0" + new Date().getDate()).slice(-2);
      this.commitProcess(3);
    },

    //  
    commitProcess(gclx) {
      if (this.fileData.length != 0 && this.fileData != null) {
        this.fileData.forEach((ele, i, arr) => {
          this.gcFiles.push({
            fjbh: ele.split("|")[0],
            fjmc: ele.split("|")[1]
          });
        });
      }
      addOrUpdateTaskProcess({
        xmbh: this.tasksInfo.xmbh,
        rwbh: this.tasksInfo.rwbh,
        gclx: gclx,
        gcms: this.ProcessValue,
        gcrq: this.date,
        gs: "",
        hfrbh: window.userName,
        hfrxm: window.userId,
        fjdata: this.fileData.join(",")
      }).then(({ data }) => {
        if (data.state == "success") {
          let obj = {
            cjrxm: window.userName,
            cjrbh: window.userId,
            cjsj: getMyDate(new Date()),
            gcms: this.ProcessValue,
            logo: window.logo,
            gclx: gclx,
            fjData: this.gcFiles
          };
          this.$alert(gclx == 1 ? "" : "", "", {
            confirmButtonText: "",
            type: "success",
            callback: action => {
              this.taskDetailProcess.unshift(obj);
              this.ProcessValue = "";
              this.fileList = [];
              this.fileData = [];
              this.gcFiles = [];
              this.filesData = [];
            }
          });
        } else {
          this.$alert(data.msg, "", {
            confirmButtonText: "",
            type: "error"
          });
        }
      });
    },

    //  

    // 
    handleRemove(file, fileList) {
      this.fileData.forEach((ele, i, arr) => {
        if (ele.includes(file.name)) this.fileData.splice(i, 1);
      });
    },

    beforeUpload(file) {
      if (this.filesArr.length > 0) {
        if (this.filesArr[0].name == file.name) return;
      }
      this.filesArr.push(file);

      let fd = new FormData();
      fd.append("fileUpload", file);
      fd.append("xmbh", this.xmbh);

      axios
        .post(window.baseurl + "attachment/uploadAttach.do", fd, {
          headers: { "Content-Type": "multipart/form-data" }
        })
        .then(res => {
          this.fileData.push(res.data.data);
        });

      return true;
    },
    handleChange(file, fileList) {
      let r = [];
      this.filesData.push(file);
      for (var i = 0, l = this.filesData.length; i < l; i++) {
        for (var j = i + 1; j < l; j++)
          if (this.filesData[i].name === this.filesData[j].name) j = ++i;
        r.push(this.filesData[i]);
      }
      this.fileList = r;
      this.$refs.upload.submit();
    },

    closeDialog() {
      this.dialog = false;
    },
    handleEditTaskSuc() {
      this.dialogEditLcbTaskVisible = !this.dialogEditLcbTaskVisible;
    },
    selectHide(e) {
      if (e.target.className != "el-input__inner") {
        this.selectShow = false;
        this.userListShow = false;
      }
    },
    searchPerson(event) {
      event.cancelBubble = true;
      this.selectShow = true;
    },
    //  
    getTaskinfo(e, params) {
      this.fbcpmc = e.currentTarget.getAttribute("data-fbcp").split("&")[0];
      this.fbcpbh = e.currentTarget.getAttribute("data-fbcp").split("&")[1];

      this.getMilestone(this.fbcpmc, this.fbcpbh);
      this.ViewTransition = true;
      this.shownindex = params;
      this.taskActive = " ";
    },
    //  
    changeZrr(e, params) {
      e.stopPropagation();
      this.cpinfo = this.taskList[params];
      this.dialogEditTaskVisible = !this.dialogEditTaskVisible;
    },
    changeCPzrr(data) {
      this.dialogEditTaskVisible = false;
      if (!data.isJf) {
        this.milestones.forEach((ele, i, arr) => {
          ele.tasks.rows.forEach((ele, i, arr) => {
            if (ele.lx != 9) {
              ele.ssrxm = data.xm;
            }
          });
        });
      }
    },

    //  
    handleDialog(data) {
      if (typeof data == "object" && data.type == "daily") {
        //
        this.taskName = data.rwmc;
        this.TaskProcess = data;
        this.dialogDailyVisible = !this.dialogDailyVisible;
      } else if (typeof data == "object" && data.type == "commit") {
        //
        this.lcbTaskDetail = data;
        this.commitlcbVisible = !this.commitlcbVisible;
      } else if (typeof data == "object" && data.type == "edit") {
        this.getTasksByCatalog(
          data.catalog,
          data.catalogId,
          data.cpbh,
          data.cpmc,
          data.catalogType
        ); //
        this.editTaskInfo = data;
        this.dialog = true;
        this.dialogEditLcbTaskVisible = !this.dialogEditLcbTaskVisible;
      }
    },

    //  
    handleTaskinfo(data) {
      this.tasksInfo = data;
      this.taskDetailProcess = [];
      this.curPageNum = 0;
      this.taskcurPage = 1;
      getTaskDto({
        //DTO
        rwbh: this.tasksInfo.rwbh
      }).then(({ data }) => {
        if (data.state == "success") {
          this.lcbTasks = data.data;
        }
      });

      if (this.tasksInfo.lx != 9) {
        queryProductParticipant({
          //
          xmbh: this.xmbh,
          cpbh: this.fbcpbh,
          cpmc: this.fbcpmc
        }).then(({ data }) => {
          if (data.state == "success") {
            this.taskCyz = [];
            if (data.state == "success") {
              data.data.forEach((ele, i, arr) => {
                if (ele.roleName == "") {
                  this.taskCyz.push(ele);
                  this.dynamicTags = this.taskCyz;
                }
              });
            }
          }
        });

        getTemplateByRwbh({
          // 
          rwbh: this.tasksInfo.rwbh
        }).then(({ data }) => {
          if (data.state == "success") {
            if (data.data != null && data.data.length != 0) {
              this.lcbTemplateFile = data.data;
            } else {
              this.lcbTemplateFile = [];
            }
          }
        });
      } else {
        // 
        querytaskParticipant({
          rwbh: this.tasksInfo.rwbh,
          xmbh: this.tasksInfo.xmbh
        }).then(({ data }) => {
          this.taskCyz = [];
          if (data.state == "success") {
            data.data.forEach((ele, i, arr) => {
              if (ele.roleName == "") {
                this.taskCyz.push(ele);
                this.dynamicTags = this.taskCyz;
              }
            });
          }
        });
      }
      // 
      this.queryProjectParticipant();

      //  ()
      this.queryTaskProcess();
      this.processTipshow = true;
      this.dialogTableVisible = !this.dialogTableVisible;
    },

    showAllprocess() {
      this.taskcurPage += 1;
      this.queryTaskProcess();
      this.processTipshow = false;
    },
    //  ()
    queryTaskProcess() {
      queryTaskProcess({
        curPage: this.taskcurPage,
        pageSize: this.taskpageSize,
        rwbh: this.tasksInfo.rwbh
      }).then(({ data }) => {
        if (data.state == "success") {
          if (data.data.rows != null) {
            this.taskDetailProcess = this.taskDetailProcess.concat(
              data.data.rows
            );
            this.taskProcessNum = data.data.records;
            this.curPageNum += data.data.rows.length;
          }
        }
      });
    },

    //  
    queryProjectParticipant() {
      queryProjectParticipant({
        xmbh: this.xmbh
      }).then(({ data }) => {
        if (data.state == "success") {
          data.data.forEach((ele, i, arr) => {
            if (ele.userName == "") {
              data.data.splice(i, 1);
            }
          });
          this.list0 = data.data;
          this.listt0 = data.data;
        }
      });
    },
    //  
    handleClose(tag) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          deleteTaskParticipant({
            xmbh: this.tasksInfo.xmbh,
            rwbh: this.tasksInfo.rwbh,
            yhbh: tag.userId
          }).then(({ data }) => {
            if (data.state == "success") {
              this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
            }
          });
        })
        .catch(() => {});
    },
    addUser(e) {
      e.stopPropagation();
      this.selectShow = !this.selectShow;
    },

    // 
    handleDailyParper(data) {
      // console.log(data)
      this.taskName = data.rwmc;
      this.dialogDailyVisible = !this.dialogDailyVisible;
    },
    // 
    handleCommitTask(data) {},
    // ()
    changeTaskStaus(data) {
      let state = "";
      if (data.split("&")[1] == 1) {
        state = 2;
      } else {
        state = 1;
      }
      changeTaskStatus({
        rwbh: data.split("&")[0],
        state: state
      }).then(({ data }) => {
        if (data.state == "success") {
          this.getTasks(1);
        }
      });
    },
    //  
    addItemTask(data) {
      this.addTaskCPxx = data;
      this.getTasksByCatalog(
        data.catalog,
        data.catalogId,
        data.cpbh,
        data.cpmc,
        data.catalogType
      ); // 
      this.dialogAddTaskVisible = !this.dialogAddTaskVisible;
    },

    //
    AddTaskCommit(data) {
      addOrUpdateTask(JSON.parse(data)).then(({ data }) => {
        if (data.state == "success") {
          this.dialogAddTaskVisible = false;
          this.$alert("", "", {
            confirmButtonText: "",
            type: "success",
            callback: action => {}
          });
        } else {
          this.$alert(data.msg, "", {
            confirmButtonText: "",
            type: "error"
          });
        }
      });
    },

    // 
    addTaskParty(e) {
      let label = e.currentTarget.getAttribute("data-label");
      let index = label.split("&")[2];
      let yhbh = label.split("&")[1];
      let yhxm = label.split("&")[0];
      let userArr = [];
      this.dynamicTags.forEach((ele, i, arr) => {
        userArr.push(this.dynamicTags[i].userName);
      });
      if (userArr.includes(label.split("&")[0])) {
        return;
      } else {
        addTaskParticipant({
          xmbh: this.xmbh,
          rwbh: this.tasksInfo.rwbh,
          yhbh: yhbh,
          yhxm: yhxm
        }).then(({ data }) => {
          if (data.state == "success") {
            this.dynamicTags.push(this.listt0[index]);
          }
        });
      }

      this.selectShow = !this.selectShow;
    },

    // 
    handleClick(tab, event) {
      this.getTasks(1);
    },
    //  
    handleSelectWc(data) {
      this.taskState = data;
      this.getTasks(1);
    },
    //  
    handleSelectSort(data) {
      this.orderby = data;
      this.getTasks(1);
    },
    // 
    handleCurrentChange(data) {
      this.getTasks(data);
    },

    //  
    handleSubmit(data) {
      // console.log(data)
      addOrUpdateTaskProcess({
        xmbh: this.TaskProcess.xmbh,
        rwbh: this.TaskProcess.rwbh,
        gclx: 2,
        gcms: data.gcms,
        gcrq: data.gcrq,
        gs: data.gs,
        fjdata: data.fjdata
      }).then(({ data }) => {
        if (data.state == "success") {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "success"
          });
          this.dialogDailyVisible = !this.dialogDailyVisible;
        }
      });
    },
    closeDailyParper() {
      this.closeDialogNum += 1;
    },
    //  
    handleCell(data) {
      getTaskDto({
        //DTO
        rwbh: data.rwbh
      }).then(({ data }) => {
        if (data.state == "success") {
          this.lcbTasks = data.data;
        }
      });
      // 
      querytaskParticipant({
        rwbh: data.rwbh,
        xmbh: data.xmbh
      }).then(({ data }) => {
        this.taskCyz = [];
        if (data.state == "success") {
          if (data.data.length != 0) {
            data.data.forEach((ele, i, arr) => {
              if (ele.roleName == "") {
                this.taskCyz.push(ele);
                this.dynamicTags = this.taskCyz;
              }
            });
          } else {
            this.dynamicTags = [];
          }
        }
      });

      // 
      queryProjectParticipant({
        xmbh: data.xmbh
      }).then(({ data }) => {
        if (data.state == "success") {
          this.taskSSry = data.data;
        }
      });

      this.dialogTableVisible = !this.dialogTableVisible;
    },

    // 
    checkMyTask(e) {
      let type = e.currentTarget.getAttribute("data-type");
      this.ViewTransition = false;
      this.shownindex = "";
      this.taskActive = type;
      this.getTasks(1);
    },

    //  
    handleEditTaskCommit(data) {
      // console.log(data)
      this.dialogEditTaskVisible = false;
    },
    // 
    handleItemUser() {
      this.userListShow = !this.userListShow;
    },
    // 
    handleAddDDgroup() {
      this.dingdingGroupVisible = !this.dingdingGroupVisible;
      this.activeName2 = "first";
      getChatId({
        // ID
        xmbh: this.xmbh
      }).then(({ data }) => {
        if (data.state == "success") {
          if (data.data != "") {
            this.chatId = data.data;
            this.dingdingShow = true;
          }
        }
      });
    },
    handleSaveChat() {
      //id
      if (this.activeName2 == "first") {
        if (this.chatId == "") {
          this.$alert("ID", "", {
            confirmButtonText: "",
            type: "warning"
          });
        } else {
          updateChatId({
            xmbh: this.xmbh,
            chatId: this.chatId
          }).then(({ data }) => {
            if (data.state == "success") {
              this.dingdingGroupVisible = false;
              this.$alert("", "", {
                confirmButtonText: "",
                type: "success"
              });
            }
          });
        }
      } else if (this.activeName2 == "second") {
        if (this.chatName == "") {
          this.$alert("", "", {
            confirmButtonText: "",
            type: "warning"
          });
        } else {
          createChat({
            xmbh: this.xmbh,
            name: this.chatName
          }).then(({ data }) => {
            if (data.state == "success") {
              this.dingdingGroupVisible = false;
              this.$alert("", "", {
                confirmButtonText: "",
                type: "success"
              });
            }
          });
        }
      }
    },
    // 
    getMilestone(cpmc, cpbh) {
      getMilestoneCatalog({
        xmbh: this.xmbh,
        cpbh: cpbh,
        cpmc: cpmc,
        curPage: 1,
        pageSize: 99999,
        taskType: this.rwlxValue,
        rwzt: this.rwztValue,
        keyword: ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.milestones = data.data;
        }
      });
    },
    // 
    getTasks(curPage) {
      getTasks({
        curPage: curPage,
        pageSize: 12,
        startDt: "",
        endDt:
          this.activeName == "first"
            ? new Date().getFullYear() +
              "-" +
              ("0" + (new Date().getMonth() + 1)).slice(-2) +
              "-" +
              ("0" + new Date().getDate()).slice(-2)
            : "",
        state: this.taskState,
        orderby: this.orderby,
        keyword: ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.records = data.data.records;
          this.myTaskArr = data.data.rows;
        }
      });
    },

    // 
    changeTaskSSry() {
      this.xgssry = !this.xgssry;
      if (!this.xgssry) {
        queryProjectParticipant({
          xmbh: this.xmbh
        }).then(({ data }) => {
          if (data.state == "success") {
            this.taskSSry = data.data;
          }
        });
      }
    },
    //  
    filterSearch(val) {
      queryUser({
        curPage: 1,
        pageSize: 9999,
        xmbh: this.xmbh,
        Isexternal: "",
        keyword: val
      }).then(({ data }) => {
        if (data.state == "success") {
          this.taskSSry = data.data.rows;
        }
      });
    },
    getTaskssry(val) {
      this.xztaskSSry.userName = val.split("&")[0];
      this.xztaskSSry.userId = val.split("&")[1];
    },

    // 
    CommitChangeTaskSSry() {
      // console.log( this.xztaskSSry)
      if (JSON.stringify(this.xztaskSSry) === "{}") {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return false;
      } else if (
        this.xztaskSSry.userId == "" ||
        this.xztaskSSry.userName == ""
      ) {
        this.$alert("", "", {
          confirmButtonText: "",
          type: "warning"
        });
        return false;
      }

      modifyTaskLeader({
        xmbh: this.xmbh,
        rwbh: this.tasksInfo.rwbh,
        yhbh: this.xztaskSSry.userId,
        yhxm: this.xztaskSSry.userName
      }).then(({ data }) => {
        if (data.state == "success") {
          this.lcbTasks.ssrxm = this.xztaskSSry.userName;
          this.xgssry = true;
          this.$alert("", "", {
            confirmButtonText: "",
            type: "success",
            callback: action => {
              let index = this.tasksInfo.index;
              this.milestones[this.tasksInfo.lcbindex].tasks.rows[
                index
              ].ssrxm = this.xztaskSSry.userName;
            }
          });
        }
      });
    },

    //  ()
    addItemuser() {
      this.getProjectCatalog();
      this.getItemUserNum();
    },

    // 
    getProjectCatalog() {
      getProjectCatalog({ xmbh: this.xmbh }).then(({ data }) => {
        if (data.state == "success") {
          this.taskList = data.data;
          if (data.data != null && data.data.length != 0) {
            this.fbcpmc = this.fbcpmc == "" ? data.data[0].cp : this.fbcpmc;
            this.fbcpbh = this.fbcpbh == "" ? data.data[0].cpbh : this.fbcpbh;
            // 
            this.getMilestone(this.fbcpmc, this.fbcpbh);
          }
        }
      });
    },
    // 
    getTasksByCatalog(catalog, catalogId, cpbh, cpmc, catalogType) {
      getTasksByCatalog({
        xmbh: this.xmbh,
        catalog: catalog,
        catalogId: catalogId,
        cpbh: cpbh,
        cpmc: cpmc,
        catalogType: catalogType,
        curPage: 1,
        pageSize: 99999,
        taskType: "1",
        rwzt: "",
        keyword: ""
      }).then(({ data }) => {
        if (data.state == "success") {
          this.LcbrwArr = data.data.rows;
        }
      });
    },

    // 
    getItemUserNum() {
      queryProjectParticipant({
        xmbh: this.xmbh
      }).then(({ data }) => {
        if (data.state == "success") {
          data.data.forEach((ele, i, arr) => {
            if (ele.userName == "") {
              data.data.splice(i, 1);
            }
          });
          this.itemUserNum = data.data.length;
        }
      });
    }
  },

  watch: {
    name(n, o) {
      var _this = this;
      var tab = this["list0"];
      if (this.name) {
        _this["listt0"] = [];
        tab.forEach(function(item, i, input) {
          if (
            tab[i].userId.indexOf(_this.name) >= 0 ||
            tab[i].userName.indexOf(_this.name) >= 0
          ) {
            _this["listt0"].push(tab[i]);
          }
        });
      } else {
        _this["listt0"] = tab;
      }
    },
    dialogTableVisible(n, o) {
      if (n) {
        this.$nextTick(function() {
          this.$refs.fileContent.appendChild(
            this.$refs.upload.$children[1].$el
          );
        });

        this.partyArr = [];
        this.listt0.forEach((ele, i, input) => {
          if (
            JSON.stringify(this.dynamicTags).indexOf(
              JSON.stringify(this.listt0[i])
            ) != -1
          ) {
            this.partyArr.push(i);
          }
        });
      }
    },
    dynamicTags(n, o) {
      this.partyArr = [];
      this.listt0.forEach((ele, i, input) => {
        if (
          JSON.stringify(this.dynamicTags).indexOf(
            JSON.stringify(this.listt0[i])
          ) != -1
        ) {
          this.partyArr.push(i);
        }
      });
    }
  },

  activated() {},
  components: {
    itemTask,
    itemtable,
    itemSelect,
    pagination,
    dailyParper,
    enditTaskInfo,
    UserBanner,
    addItemUser,
    editLcbTasks,
    // templateTabel,
    fileTable,
    milestoneGl,
    commitMilestone,
    question,
    complain,
    overview
  }
};
</script>
