  cursor: pointer;
  background: #eee;
}
.project_task_detail_taskbar ul.task_list li span {
  color: #363748;
  font-size: 14px;
}

.project_task_detail_taskbar ul.task_complete_status li {
  padding: 10px;
  font-weight: 700;
}

.project_task_milestone {
  padding: 10px 0;
  overflow-x: auto;
  height: calc(100vh - 80px);
  width: calc(100% - 300px);
  position: fixed;
  left: 300px;
}
.project_task_milestone .milestone-list {
  width: 100%;
  display: flex;
  overflow-x: auto;
}

.on_active {
  color: #3da8f5;
}

.project_task_detail .item_zrr {
  display: inline-block;
  width: 20px;
  height: 20px;
  line-height: 20px;
  border-radius: 50%;
  text-align: center;
  background: #3ba7f5;
  color: #fff !important;
  font-size: 12px !important;
}
.project_task_detail .item_zrr_YF {
  background: #7ece64;
}

.task-detail-dialog-header {
  border-bottom: 1px solid #ccc;
  padding: 0 20px;
}
.task-detail-dialog-header p {
  margin: 10px 0 !important;
}
.task-detail-dialog-header h2 {
  font-weight: normal;
}
.task-detail-scenario-infos-wrap {
  border-bottom: 1px solid #ccc;
  padding: 0 20px;
}
.task-detail-scenario-infos-wrap > p {
  margin: 10px 0 !important;
}
.task-detail-scenario-infos-wrap p > span:nth-of-type(1) {
  margin-right: 20px;
  color: #a6a6a6;
}
.detail-activities-wrap {
  background: #f5f5f5;
  padding: 0 20px;
}
.involve-view {
  border-bottom: 1px solid #ccc;
  padding: 10px 0 20px 0;
}
.involve-view button {
  border-radius: 50%;
  width: 30px;
  height: 30px;
  padding: 0;
  font-size: 20px;
}
.el-tag {
  margin-right: 5px;
}
.activities-list-wrap {
  padding-bottom: 120px;
}
.activities-list-wrap .taskProcess-tips {
  text-align: center;
}
.activities-list-wrap .taskProcess-tips > a:hover {
  cursor: pointer;
  color: #8da8f5 !important;
}
.activities-list-wrap .taskProcess-tips > a {
  display: block;
  color: #3da8f5;
  margin: 5px 20px;
}

.activities-list-wrap ul .task-detail-process {
  border-bottom: 1px solid #ccc;
}
.activities-list-wrap ul .task-detail-process p {
  font-size: 13px;
}
.activities-list-wrap ul li {
  padding: 0 10px 5px 20px;
}
.activities-list-wrap ul li p::after {
  content: "";
  display: block;
  clear: both;
}
/* .activities-list-wrap ul li>div>p:nth-of-type(1){
  float: left;
  border: 1px solid #000;
}
.activities-list-wrap ul li>div>p:nth-of-type(2){
  float: right;
} */
.activity-creator {
  width: 100%;
  background: #e5e5e5;
  position: absolute;
  bottom: 0;
}
.activity-creator .activity-creator-content {
  margin: 14px 20px 13px 15px;
}
.activity-creator-operate {
  background: #ffffff;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  border-top: none;
  height: 40px;
  line-height: 40px;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
}
/* .activity-creator-operate:after{
    content: "";
  display: block;
  clear: both;
} */
.activity-creator-operate button.el-button {
  width: 60px;
  height: 30px;
  margin-top: 5px;
  padding: 0;
  margin-left: 0;
}
.task_participant {
  position: absolute;
  background: #fff;
  width: 220px;
  box-shadow: 0 0 5px #ccc;
  padding-top: 10px;
  z-index: 1000;
}

.task_participant li:hover {
  background: #e5e5e5;
  cursor: pointer;
}
.task_participant li {
  padding: 2px 15px;
}
.task_participant .invite_newPerson {
  color: #549ef6;
  font-weight: 700;
}
.task_participant .invite_newPerson span {
  display: inline-block;
  vertical-align: middle;
  font-size: 26px;
  color: #549ef6;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
/**/
.project_task_zrr {
  box-shadow: 0 0 5px #999;
  background: #fff;
  width: 50%;
  position: absolute;
  right: 0;
  z-index: 333;
}
.project_task_zrr li {
  font-size: 14px;
  color: #666;
  margin: 5px 0;
  display: flex;
}
.project_task_zrr li input {
  width: 60%;
  background: #eee;
  outline: none;
  border: none;
}
.project_task_detail_cpmc {
  white-space: nowrap;
}
.process-daily {
  width: 470px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  float: left;
  margin-top: 7px;
  font-size: 12px;
  color: #999;
}
.LcbTemplate {
  color: #549ef6;
  margin-right: 20px;
}
.LcbTemplate:hover {
  cursor: pointer;
  text-decoration: underline;
}
.process-fjlist {
  margin-right: 20px;
  color: #549ef6;
}
.process-fjlist:hover {
  cursor: pointer;
  text-decoration: underline;
}
.process-gclx {
  font-size: 12px;
  border-radius: 3px;
}
.gclx-rz {
  background: rgba(59, 167, 245, 0.2);
  color: #3ba7f5;
  border: 1px solid #3ba7f5;
}
.gclx-cb {
  background: rgba(245, 108, 108, 0.2);
  color: #f56c6c;
  border: 1px solid #f56c6c;
}
.project_task_itemInfo {
  display: inline-block;
  width: 25vw;
  min-width: 270px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-top: 16px;
}
</style>



